import { redirect } from "next/navigation";
import { prisma } from "~/server/db";
import LoginForm from "./_components/LoginForm";

async function loginWithCode(formData: FormData) {
  "use server";

  const code = formData.get("code") as string;
  const team = await prisma.team.findFirst({
    where: {
      id: parseInt(code),
    },
  });

  if (team) {
    redirect(`/play/${team.id}`);
  }

  return { error: "Invalid team code" };
}

export default function Home() {
  return (
    <div className="min-h-screen bg-gray-100 flex flex-col items-center justify-center p-4">
      <div className="max-w-md w-full">
        <div className="text-center mb-8">
          <h1 className="text-4xl font-bold text-gray-900 mb-2">ARG Game</h1>
          <p className="text-gray-600">Enter your team code to start playing</p>
        </div>

        <div className="bg-white rounded-lg shadow-lg p-6">
          <LoginForm loginWithCode={loginWithCode} />

          <div className="mt-6 text-center">
            <a
              href="/admin"
              className="text-sm text-blue-600 hover:text-blue-500"
            >
              Admin Login
            </a>
          </div>
        </div>
      </div>
    </div>
  );
}
